<template>
  <div id="shops_div">
    <div id="shops_sons1">

      <div>
        <p>{{this.madata.name}}</p>
        <p>
          <van-rate v-model="this.madata.score"
                    allow-half
                    void-icon="star"
                    void-color="#eee"
                    :size="15"
                    color="#FFC300"
                    readonly />
          <span style="font-size: 8px;">月售{{this.madata.sellCount}}单</span>
        </p>
      </div>

      <div @click="show=!show"
           style="width:50px;text-align:center">
        <van-icon name="like-o"
                  v-show="show" />
        <van-icon name="like"
                  color="red"
                  v-show="!show" />
        <p style="color:#909090;font-size:14px;">{{show? "收藏" : "已收藏"}}</p>
      </div>

    </div>
    <div id="shops_sons2">
      <div>
        <span>起送价</span>
        <p>{{this.madata.minPrice}}<label>元</label></p>
      </div>
      <div>
        <span>商家配送</span>
        <p>{{this.madata.deliveryPrice}} <label>元</label></p>
      </div>
      <div>
        <span>平均配送时间</span>
        <p>20 <label>分钟</label> </p>
      </div>
    </div>
    <div id="shops_sons3">
      <h3>活动与公告</h3>
      <van-notice-bar wrapable
                      :scrollable="false"
                      :text="this.madata.bulletin" />
      <p v-for="(item,index) in this.madata.supports"
         :key="index"
         style="font-size:14px;line-height: 25px;">
        <van-tag mark
                 type="primary">减</van-tag>{{item}}
      </p>
    </div>

  </div>
</template>

<script>
import { shop_seller } from '@/api/api'
export default {
  created () {
    shop_seller().then(res => {
      console.log(res.data.data);
      this.madata = res.data.data
    })
  },
  data () {
    return {
      like: 0,
      // 收藏图标部分
      checked: true,
      show: true,
      //店铺资料
      madata: {}
    }
  },
}
</script>

<style lang="less" scoped>
#shops_div {
  padding: 25px;
  #shops_sons1 {
    display: flex;
    justify-content: space-between;
    p {
      padding-bottom: 15px;
      span {
        font-size: 12px;
        margin-left: 15px;
        color: #909090;
      }
    }
    .img-icon {
      height: 20px;
    }
  }
  #shops_sons2 {
    display: flex;
    justify-content: space-between;
    text-align: center;
    margin: 20px 0;
    span {
      font-size: 12px;
      color: #909090;
    }
    p {
      font-size: 18px;
      label {
        font-size: 12px;
        color: #909090;
      }
      #shops_sons3 {
        .van-notice-bar {
          padding: 0 !important;
        }
      }
    }
  }
}
</style>